import Vue from 'vue';
import Router from 'vue-router';
import HomePage from '@/components/HomePage.vue';
import ResumeForm from '@/components/ResumeForm.vue';
import JobSearch from '@/components/JobSearch.vue';
import AboutUs from '@/components/AboutUs.vue';
import Login from '@/components/Login.vue';
import JobProvider from '../components/JobProvider.vue';
import TestSelect from '@/components/TestSelect.vue'; // or '../components/TestSelect.vue'


Vue.use(Router);

export default new Router({
  mode: 'history', // <-- Use history mode here
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: HomePage,
    },
    {
      path: '/job-recommendation',
      name: 'ResumeForm',
      component: ResumeForm,
    },
    {
      path: '/job-search',
      name: 'JobSearch',
      component: JobSearch,
    },
    {
      path: '/about-us',
      name: 'AboutUs',
      component: AboutUs,
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/job-provide',
      name: 'JobProvider',
      component: JobProvider,
    },
    {
      path: '/test-select',
      name: 'TestSelect',
      component: TestSelect // Define route for TestSelect
    }
  ],
});
