<template>
  <v-container>
    <!-- Include the Navbar component -->
    <Navbar />

    <h1>Login Page</h1>

    <!-- Username/Email Text Box -->
    <v-text-field
      label="Username / E-mail"
      outlined
      clearable
      class="input-field"
    ></v-text-field>

    <!-- Password Text Box with Eye Icon -->
    <v-text-field
      label="Password"
      :type="showPassword ? 'text' : 'password'"
      outlined
      clearable
      class="input-field"
      append-icon="mdi-eye"
      @click:append="togglePasswordVisibility"
    ></v-text-field>

    <!-- Role Selection Box -->
    <v-select
      v-model="selectedRole"
      :items="roles"
      label="Select Your Role"
      outlined
      class="input-field"
    ></v-select>

    <!-- Login Button -->
    <v-btn
      class="login-button"
      style="background-color: black; color: white;"
    >
      Login
    </v-btn>

    <!-- Create Account Link -->
    <v-btn text color="secondary" class="create-account" @click="goToSignup">
      Create a new account
    </v-btn>
  </v-container>
</template>

<script>
import Navbar from './Navbar.vue';

export default {
  name: 'Login',
  components: {
    Navbar
  },
  data() {
    return {
      roles: ['Job Seeker', 'Job Provider'],
      selectedRole: null,
      showPassword: false
    };
  },
  methods: {
    togglePasswordVisibility() {
      this.showPassword = !this.showPassword;
    },
    goToSignup() {
      this.$router.push('/signup');
    }
  }
};
</script>

<style scoped>
.input-field {
  margin-bottom: 20px;
}

.login-button {
  margin-top: 20px;
}

.create-account {
  margin-top: 10px;
  text-decoration: underline;
  cursor: pointer;
}
</style>
